//Portfolio Styles
a:hover,
a:visited,
a:active,
a:link {
    text-decoration: none;
    color: #000;
}

// blue link hover
.link--blue {
    color: #3b35fa !important;
}

.link--blue:hover {
    text-decoration: underline !important;
}

// send and resume links
.submit-button:hover {
    color: #fff;
    background: #000;
}
.resume_button:hover {
    color: #fff;
    background: #000;
}

// navbar links
.navbar-default .navbar-brand:focus,
.navbar-default .navbar-brand:hover {
    background-color: black;
    i {
        color: #fff;
    }
}

.navbar-default .navbar-nav > li > a:focus,
.navbar-default .navbar-nav > li > a:hover {
    color: white;
    background-color: black;
}

// swiper
.swiper-container {
    width: 100%;
    height: 300px;
}
.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.resume_button {
    text-align: center;
    margin: 0 auto;
    border: 1px solid #000;
    padding: 6px 18px;
    color: #000;
    text-decoration: none;
    border: 1px solid #000;
    border-radius: 3px;
    font-family: 'Josefin Sans', sans-serif;
}
.center {
    margin: 0 auto;
    width: 136px;
    margin-bottom: 120px;
}
.uppercase {
    text-transform: uppercase;
}
// Additional projects
.additional-projects-conatiner {
    text-align: center;
    margin-top: 50px;
    margin-bottom: 50px;
    p {
        font-family: Josefin Sans, sans-serif;
    }
}
#footer {
    background-color: #000;
    height: 300px;
    padding-top: 140px;
}
.devpost-img {
    background-color: white;
    width: 32px;
    margin-top: -10px;
}
.navbar-default {
    background-color: $gray-bg;
    height: 50px;
}
.navbar-default .navbar-header .navbar-brand {
    color: black;
}
.navbar-default .navbar-nav li > a {
    color: black;
}
.navbar-default .navbar-collapse,
.navbar-default .navbar-form {
    background-color: $gray-bg;
}
body {
    font-family: 'Alfa Slab One', cursive;
    background-color: $gray-bg; //#fbb03b;
}
.row {
    display: flex;
    flex-wrap: wrap;
}

.displayBox {
    background-color: rgb(187, 187, 187);
    border-radius: 10px;
    text-align: center;
    padding: 30px;
    display: flex;
    flex-wrap: wrap;
    font-family: 'Josefin Sans', sans-serif;
}
.titles h2 {
    font-size: 50px;
    margin-top: 100px;
    margin-bottom: 0px;
    text-align: center;
    color: #000; //$secondary-color;
}
.cont {
    padding: 80px 20px 100px 20px;
    text-align: center;
    color: #000;
    font-weight: 300;
}
.section-2 {
    // !important;height: 1000px;
    height: auto;
    background-repeat: no-repeat;
    -moz-background-size: cover;
    -o-background-size: cover;
    -webkit-background-size: cover;
    background-size: cover;
    position: relative;
    background-position: 50% 50%;
}
.no-webp .section-2 {
    background-image: url(../images/bwWebDev.jpeg);
}
.webp .section-2 {
    background-image: url(../images/webp/bwWebDev.webp);
}
.no-js .section-2 {
    background-image: url(../images/bwWebDev.jpeg);
}
.section-3 {
    // height: 1000px!important;
    background-repeat: no-repeat;
    -moz-background-size: cover;
    -o-background-size: cover;
    -webkit-background-size: cover;
    background-size: cover;
    position: relative;
    background-position: 50% 50%;
    input.form-control,
    text-area.form-control {
        width: 100%;
    }
}
.input-group {
    width: 50%;
}
fieldset {
    width: 100%;
}
input.submit-button {
    border: 1px solid #000;
    padding: 4px 18px;
}
.imgWrap {
    position: relative;
    height: auto;
    width: auto;
    overflow: auto;
}
.imgWrap img {
    height: auto;
    width: 100%;
}
.imgDescription {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.82);
    color: #fff;
    text-align: center;
    opacity: 0;
    font-family: 'Josefin Sans', sans-serif;
    -webkit-transition: opacity 0.2s;
    padding: 14px 18px;
    margin-top: -100%;
    -webkit-transition: margin-top 0.6s;
}
.imgWrap:hover .imgDescription {
    opacity: 1;
    margin-top: 0;
}
.socialMedia {
    margin: 0 auto;

    ul {
        margin: 0 auto;
        list-style-type: none;
        padding-left: 0px;
        li {
            display: inline-block;
            padding-right: 12px;
            padding-left: 12px;
        }
    }
}
.socialMedia a {
    color: #fff;
}
.socialMedia a:hover {
    color: #bbb;
}
a {
    margin: 0 auto;
}
#about .displayBox {
    display: table;
    text-align: center;
}
.textBox {
    font-size: 20px;
    display: table-cell;
    vertical-align: middle;
    /*font-family: 'Varela Round', sans-serif;*/
    font-family: 'Josefin Sans', sans-serif;
}
#about .titles h2 {
    margin-top: 120px;
    color: #000; //$secondary-color;//#f15a22
}
#contact .titles h2 {
    color: #000; //$secondary-color;//#f15a22
    margin-bottom: 100px;
}
#resume .titles h2 {
    color: #000;
    margin-bottom: 100px;
    margin-top: 0px;
}

.portfolio-wrapper {
    overflow: hidden;
    width: 100%;
}
.portfolio-wrapper .button,
.portfolio-wrapper .button:hover {
    background-color: rgba(0, 0, 0, 0.82);
    color: #fff;
    padding: 3px 8px;
    text-decoration: none;
    border: 1px solid #fff;
    border-radius: 3px;
}
.portfolio-wrapper .button,
.portfolio-wrapper .button:focus {
    background-color: white;
    color: black;
    padding: 3px 8px;
    text-decoration: none;
    border: 1px solid #fff;
    border-radius: 3px;
}
.portfolio-wrapper ul {
    list-style: none;
    padding-left: 0px;
}
.portfolio-item {
    margin-left: 25%;
    margin-bottom: 20px;
    width: 50%;
    display: block;
    position: relative;
    float: left;
}
.portfolio-item .imgWrap {
    position: absolute;
    background: rgba(54, 54, 54, 0.4);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    cursor: pointer;
    opacity: 0;
    visibility: hidden;
    transition: visibility, opacity 1s;
    -webkit-transition: visibility, opacity 1s;
}
.portfolio-item:hover,
.portfolio-item:focus,
.portfolio-item:active {
    .imgWrap {
        visibility: visible;
        opacity: 1;
    }
}
#projects-title {
    margin-bottom: 40px;
    color: #000; //$secondary-color;
}
#projects {
    background-color: $gray-bg;
}
.project-img {
    width: auto;
}
.textContent {
    text-align: left;
}
u {
    text-decoration: underline;
}

// Navigation Hamburger Icon
.container-button {
    display: inline-block;
    cursor: pointer;
    float: right;
    margin-top: 4px;
    margin-right: 20px;
}

.bar1,
.bar2,
.bar3 {
    width: 35px;
    height: 5px;
    background-color: #000;
    margin: 6px 0;
    transition: 0.4s;
}

.change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {
    opacity: 0;
}

.change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
}

/* Media Quaries */

@media only screen and (max-width: 568px) {
    .cont h1 {
        font-size: 32px;
        margin-top: -64px;
    }
    .cont h2 {
        font-size: 20px;
    }
    .imgDescription h2 {
        font-size: 18px;
    }
    .imgDescription p {
        font-size: 11px;
    }
    .portfolio-item {
        width: 100%;
        height: auto;
        float: left;
    }
    .titles h2 {
        font-size: 34px;
    }
    .textContent {
        font-size: 16px;
    }
    .portfolio-wrapper ul li.portfolio-item {
        width: 75%;
        margin-left: 12.5%;
    }
    .input-group {
        width: 100%;
    }
    .section-1 {
        max-height: 700px;
    }
}
@media only screen and (min-width: 768px) {
    .container-button {
        display: none;
    }
}
@media only screen and (min-width: 992px) {
    .portfolio-item {
        max-width: 500px;
        margin: 0 auto 20px auto !important;
        clear: both;
        float: none !important;
    }
}
